.benchmarks {
}

/*
BANNER
*/

.benchmarksBanner {
    padding: 1rem;
    padding-top: calc(var(--navHeight) + 1rem);
    background-color: var(--themeColorAccentNeg10);
}

.benchmarksBanner a {
    display: flex;
}

.benchmarksBanner a:hover {
    color: var(--themeColorPrimary);
    text-decoration: underline;
}

.benchmarksBanner svg {
    font-size: .6rem;
    align-self: center;
    margin-right: .2rem;
}

.benchmarksPDFViewer {
    padding: 1rem;
    padding-top: calc(var(--navHeight) + 1rem);
    background-color: var(--themeColorAccentNeg10);
    height: 1000px;
    display: flex;
  justify-content: center;
}


.benchmarksPDFViewer a {
    display: flex;
}

.benchmarksPDFViewer a:hover {
    color: var(--themeColorPrimary);
    text-decoration: underline;
}

.benchmarksPDFViewer svg {
    font-size: .6rem;
    align-self: center;
    margin-right: .2rem;
}

.paginationViewer {
    display: flex;
    flexDirection: row;
    position: absolute;
}

.PDFViewer {
    height: 700px;
    position: relative;
}

.paginationButton {
    display: block;
    border: 1.5px solid white;
    padding: .2rem .6rem;
    border-radius: 5px;
    transition:
        background-color .3s,
        border-color .3s;
}

.paginationButton:hover {
    cursor: pointer;
    background-color: var(--themeColorEdammoBlue);
    border-color: var(--themeColorEdammoBlue);
    color: white;
}