.nav {
    display: flex;
    width: 100%;
    font-size: .5rem;
    justify-content: right;
    padding: 0 .2rem;
    align-items: center;
    height: var(--navHeight);
    position: fixed;
    transform: translateZ(0);
    z-index: 100;
    opacity: 1;
    transition:
        background-color .2s,
        opacity .4s;
}

.nav_logo {
    position: absolute;
    left: .5rem;
    display: flex;
    align-items: center;
}

.nav_logoText {
    font-weight: light;
    font-size: 1rem;
}

.nav_logo > img {
    margin-right: 1.5ch;
}

.nav--opaque {
    background-color: var(--themeColorDark);
    box-shadow: 0 0 8px 0 rgba(35, 35, 35, 1);
}

.nav--hidden {
    opacity: 0;
    pointer-events: none;
}

.nav_nonlink,
.nav a {
    display: inline;
    color: white;
    margin-right: 1rem;
    transition: color .3s;
    padding: .5rem 0rem;
}

a.nav_link--active {
    color: var(--themeColorEdammoBlue);
}

.nav a:hover {
    color: var(--themeColorEdammoBlue);
}

.nav_contactbutton {
    border: 1.5px solid white;
    padding: .2rem .6rem;
    border-radius: 5px;
    transition:
        background-color .3s,
        border-color .3s;
}

.nav_contactbutton:hover {
    cursor: pointer;
    background-color: var(--themeColorEdammoBlue);
    border-color: var(--themeColorEdammoBlue);
    color: white;
}